limel-banner {
    --banner-icon-fill-color: rgb(var(--color-yellow-lighter));
    --banner-icon-background-color: rgb(var(--color-red-default));
    position: fixed;
    right: 0;
    top: 0;
    left: 0;
    z-index: 112;

    @media (max-width: 720px) {
        position: absolute;
    }

    limel-button:not(last-child) {
        margin-right: 0.625rem;
    }
}
